import React from "react";
import { Button } from "semantic-ui-react";

export default class Project {
    href?: string;
    name?: string;
    description?: string;
    type?: "engine" | "game" | "tool";
    tags?: Array<string>;

    original_date?: {
        from: string
        to: string
    };
    original_project_location?: string;
    original_goal?: string;
    original_language?: string;
    original_project_name?: string;
    rework_project_location?: string;
    rework_language?: string;
    rework_project_name?: string;
    rework_goal?: string;
    content?: ContentObject[];

    public getNameAsTitle(): string | undefined {
        return this.name?.replace(/_/g, ' ').toUpperCase();
    }

    public async load_project_details(): Promise<Project | false> {
        const project_detail_paths = require("../projects/*/details.json");
        if (this.name && this.name in project_detail_paths) {
            let details: string = (project_detail_paths[this.name]);
            Object.assign(this, details);
            return this;
        }
        return false;
    }

    public async load_contents(): Promise<Project | false> {
        if (!this.content || !this.name) return false;
        const projects_contents = require("../projects/*/content/*.*");
        if (this.name in projects_contents === false) return this;
        const project_contents = projects_contents[this.name];
        const promises = this.content.map(async (content_object: ContentObject): Promise<Project> => {
            const content_file_descriptor = content_object.src.split('.');
            const this_file_url_part1 = (project_contents[content_file_descriptor[0]]);
            if (!this_file_url_part1) return this;
            const this_file_url = (this_file_url_part1[content_file_descriptor[1]]);
            if (!this_file_url) return this;
            const relative_url = window.location.pathname.split('/').slice(0, -1).join('/') + this_file_url;
            return fetch(relative_url).then(async (response: Response) => {
                if (response.status < 200 || response.status > 299) throw "Invalid Http-Response when fetching data.";
                switch (content_object.type) {
                    case 'text': return response.text().then((text) => {
                        content_object.value = text;
                        return this;
                    });
                    case 'html': return response.text().then((html) => {
                        content_object.value = (<div dangerouslySetInnerHTML={{ __html: html }}></div>);
                        return this;
                    });
                }
                throw new Error('Invalid Content Type');
            });
        });
        return Promise.all(promises).then();
    }
}

export interface ContentObject {
    type: string;
    src: string;
    value?: any;
}