
import React from "react"
import Project from "./Project";
import ProjectExcerpt from "./ProjectExcerpt";

interface ProjectsOverviewProperties {
    onSelectProject: (name: string) => void;
}

interface ProjectsOverviewState {
    projects: Array<Project>;
}

export default class ProjectOverview extends React.Component<ProjectsOverviewProperties> {
    state: ProjectsOverviewState = {
        projects: []
    }

    constructor(props: ProjectsOverviewProperties) {
        super(props);
        const project_paths = require("../projects/*/details.json") as { [name: string]: string };
        for (const project_name in project_paths) {
            let project = new Project();
            const project_path = project_paths[project_name];
            project.name = project_name;
            project.href = "/projects/" + project_name + "/";
            project.load_project_details().then(() => {
                this.forceUpdate();
                console.log('update');
            });

            this.state.projects.push(project);
        }
    };


    render() {

        return (<main>
            <h1>
                Ideen Overview
            </h1>

            <ol>
                {
                    this.state.projects.map((project, index) => (
                        <li key={index}>
                            <ProjectExcerpt project={project} onClick={(event, excerpt) => {
                                this.props.onSelectProject(project.name ?? 'failed');
                            }} />
                        </li>
                    ))
                }
            </ol>
        </main>);
    }
}
