import React from "react"
import { Card } from "semantic-ui-react"
import Project from "./Project";

interface ProjectExcerptProperties {
    project: Project;
    onClick?: (event: React.MouseEvent<HTMLAnchorElement>, target: ProjectExcerpt) => void
}

export default class ProjectExcerpt extends React.Component<ProjectExcerptProperties> {
    constructor(props: ProjectExcerptProperties) {
        super(props);
    }

    render() {
        return (<Card onClick={(event) => { this.props.onClick ? this.props.onClick(event, this) : null; }}>
            <Card.Content>
                <Card.Header>
                    {this.props.project.getNameAsTitle()}
                </Card.Header>
                <Card.Description>
                    {this.props.project.description}
                </Card.Description>
                <Card.Meta>
                    {(this.props.project.tags) ? this.props.project.tags.join(', ') : ''}
                </Card.Meta>
            </Card.Content>
        </Card>);
    }
}