import React from "react"
import Project from "./Project";
import { Loader, Button, Label } from "semantic-ui-react";

interface ProjectDetailProperties {
    project_name: string
}

enum ProjectDetailLoadingState {
    LOADING,
    FAILED,
    READY
}

interface ProjectDetailState {
    loading_state: ProjectDetailLoadingState,
    project?: Project,
}

export default class ProjectDetail extends React.Component<ProjectDetailProperties> {
    state: ProjectDetailState = {
        loading_state: ProjectDetailLoadingState.LOADING,
    }

    constructor(props: ProjectDetailProperties) {
        super(props);
    }

    componentDidMount() {
        let project: Project = new Project();
        project.name = this.props.project_name;
        let promises: Array<Promise<Project | false>> = [
            project.load_project_details().then(() => {
                return project.load_contents().then(() => this.forceUpdate()), project;
            }),
        ];
        Promise.all(promises).then(() => {
            console.log(project);
            this.setState({
                project: project,
                loading_state: ProjectDetailLoadingState.READY
            });
        }).catch(() => {
            this.setState({ loading_state: ProjectDetailLoadingState.FAILED });
        });
    }

    render() {
        switch (this.state.loading_state) {
            case ProjectDetailLoadingState.LOADING:
                return (<Loader active={true}>Loading ...</Loader>);
            case ProjectDetailLoadingState.FAILED:
                return (<div>
                    <Label basic color="red" >Failed to load project: {this.props.project_name}</Label>
                    <Button active={true} href="/">Back to Overview</Button>
                </div>);
            case ProjectDetailLoadingState.READY:
                return this.render_ready();
        }
    }

    render_ready() {
        if (!this.state.project) throw new Error("Project is not loaded yet.");
        return (
            <div className="project-details">
                <h1>
                    {this.state.project.getNameAsTitle()}
                </h1>
                <ol>
                    {this.state.project.tags ? this.state.project.tags.map((tag: string) =>
                        <li key={tag}>
                            {tag}
                        </li>
                    ) : ''}
                </ol>
                <div className="content">
                    <div>
                        <h2>Original: {this.state.project.original_project_name || "No Name"}</h2>
                        <div>{this.state.project.original_language || "Not decided"}</div>
                        <p>
                            {this.state.project.original_goal}
                        </p>
                    </div>
                    <div>
                        <h2>Rework: {this.state.project.rework_project_name || "No Name"}</h2>
                        <div>{this.state.project.rework_language || "Not decided"}</div>
                        <p>
                            {this.state.project.rework_goal}
                        </p>
                    </div>
                    <div>
                        {this.state.project.content?.map((content_object, index) => {
                            if (content_object.value === undefined) {
                                return (<p key={content_object.src}>Loading ...</p>);
                            } else {
                                switch (content_object.type) {
                                    case "text":
                                        return (<pre key={content_object.src}>{content_object.value}</pre>);
                                    case "html":
                                        return (<p key={content_object.src} dangerouslySetInnerHTML={{ __html: content_object.value }}></p>);
                                    default:
                                        return (<p key={content_object.src}>Unkown Type</p>);
                                }
                            }
                        })}
                    </div>
                </div>
            </div >
        );
    }
}