import React from "react"
import ProjectOverview from "./ProjectsOverview";
import {
    BrowserRouter,
    Switch,
    Route,
    Link,
    useRouteMatch
} from "react-router-dom";
import ProjectDetail from "./ProjectDetail";

interface AppProperties {
    projects: Array<string>;
}

interface AppState {
    selected_project: string | null;
}

export default class App extends React.Component<AppProperties> {
    state: AppState = {
        selected_project: null
    }

    constructor(props: AppProperties) {
        super(props);

    }

    componentDidMount() {

    }

    render() {
        if (!this.state.selected_project) {
            return (<ProjectOverview onSelectProject={(project_name: string) => {
                this.setState({ selected_project: project_name });
                console.log(project_name);
            }}></ProjectOverview >);
        }
        return (<ProjectDetail project_name={this.state.selected_project} />);
    }
}